<script setup lang="ts">
import { breakpointsTailwind, useBreakpoints } from "@vueuse/core";
import { getLineBreaksStyle } from "~/utils/typography";

export interface EventStatsScoreboardProps {
  /**
   * If true, the component will be set to full screen view.
   */
  fullSize?: boolean;
  /**
   * Home team name.
   */
  team1Name?: string;
  /**
   * Away team name.
   */
  team2Name?: string;
  /**
   * Home team score.
   */
  team1Score?: string;
  /**
   * Away team score.
   */
  team2Score?: string;
  /**
   * Home team color.
   */
  team1Color?: string;
  /**
   * Home team color.
   */
  team2Color?: string;
  /**
   * Home team logo URL.
   */
  team1LogoUrl?: string;
  /**
   * Away team logo URL.
   */
  team2LogoUrl?: string;
}

const props = defineProps<EventStatsScoreboardProps>();
const breakpoints = useBreakpoints(breakpointsTailwind);
const greaterOrEqualMd = breakpoints.greaterOrEqual("md");
const logoSize = computed(() => props.fullSize && greaterOrEqualMd.value ? 64 : 48);
const breakWordStyleHomeTeam = computed(() => getLineBreaksStyle(props.team1Name));
const breakWordStyleAwayTeam = computed(() => getLineBreaksStyle(props.team2Name));
</script>

<template>
  <div class="grid grid-cols-3 gap-4 p-4">
    <div class="flex items-center gap-2" :class="[props.fullSize ? 'justify-center sm:justify-end' : 'justify-center']">
      <div
        class="text-base font-bold text-main xl:text-2xl ltr:text-right rtl:text-left"
        :class="[props.fullSize ? 'hidden sm:block' : 'hidden', breakWordStyleHomeTeam]"
      >
        {{ props.team1Name }}
      </div>
      <div>
        <BaseLogo
          :title="props.team1Name"
          :src="props.team1LogoUrl"
          :icon-background="props.team1Color"
          :width="logoSize"
          :height="logoSize"
          no-background
          with-initials
          fit="contain"
        />
      </div>
    </div>

    <EventStatsScore
      :class="[props.fullSize ? 'text-xl sm:text-[40px]' : 'text-xl sm:text-2xl']"
      :home-team-score="props.team1Score"
      :away-team-score="props.team2Score"
    />

    <div class="flex items-center gap-2" :class="[props.fullSize ? 'justify-center sm:justify-start' : 'justify-center']">
      <div>
        <BaseLogo
          :src="props.team2LogoUrl"
          :icon-background="props.team2Color"
          :title="props.team2Name"
          :width="logoSize"
          :height="logoSize"
          no-background
          with-initials
          fit="contain"
        />
      </div>
      <div
        class="text-base font-bold text-main xl:text-2xl ltr:text-right rtl:text-left"
        :class="[props.fullSize ? 'hidden sm:block' : 'hidden', breakWordStyleAwayTeam]"
      >
        {{ props.team2Name }}
      </div>
    </div>

    <div class="col-span-3" :class="[props.fullSize ? 'block sm:hidden' : 'block']">
      <div class="grid grid-cols-3">
        <div class="font-medium text-center text-main" :class="breakWordStyleHomeTeam">
          {{ props.team1Name }}
        </div>
        <div />
        <div class="font-medium text-center text-main" :class="breakWordStyleAwayTeam">
          {{ props.team2Name }}
        </div>
      </div>
    </div>
  </div>
</template>
