<script lang="ts" setup>
const props = withDefaults(
  defineProps<{
    min?: number;
    max?: number;
    appearDelay?: number;
    value: number;
    valueSuffix?: string;
    startProgressFrom?: "left" | "right";
    valuePosition?: "left" | "right";
    hasValueIndication?: boolean;
    valueIndicatorClass?: boolean | string | string[];
    label?: string | number;
    isLowValue?: boolean;
  }>(),
  {
    appearDelay: 0,
    min: 0,
    max: 100,
    valuePosition: "left",
  },
);

const emit = defineEmits(["value-indicator-clicked"]);
const isProgressBarVisible = ref(false);
const progressBarValue = ref(0);
const progressPercentage = computed(() => (progressBarValue.value * 100) / props.max);
const value = computed(() => props.valueSuffix ? `${progressBarValue.value}${props.valueSuffix}` : `${progressBarValue.value}`);

onMounted(() => {
  setTimeout(() => (isProgressBarVisible.value = true), 300 + props.appearDelay);
  setTimeout(() => (progressBarValue.value = props.value), 500 + props.appearDelay);
});
</script>

<template>
  <div class="flex">
    <div v-if="props.valuePosition === 'left'" class="flex w-full justify-start sm:w-[unset]">
      <span
        v-if="props.hasValueIndication"
        class="text-[#151515] dark:text-white text-sm sm:text-base font-medium w-16 text-start mb-2 sm:mb-0 truncate"
        :class="props.valueIndicatorClass"
        :title="value"
        @click="emit('value-indicator-clicked')"
      >
        {{ value }}
      </span>
    </div>
    <div
      class="flex flex-1 min-w-[4rem] h-2 bg-[#eeeeee] rounded-full dark:bg-[#0f1624]"
      :class="props.startProgressFrom === 'right' ? 'justify-end' : 'justify-start'"
    >
      <Transition
        enter-from-class="opacity-0"
        leave-to-class="opacity-0"
        enter-active-class="transition-all duration-500"
        leave-active-class="transition-all duration-500"
      >
        <div
          v-if="isProgressBarVisible"
          class="h-2 transition-all rounded-full"
          :class="[props.isLowValue ? 'bg-[#afb6c1] dark:bg-[#565f6f]' : 'bg-[var(--base-customer-color)]']"
          :style="{ width: `${progressPercentage}%` }"
        />
      </Transition>
    </div>
    <div v-if="props.valuePosition === 'right'" class="flex w-full justify-end sm:w-[unset]">
      <span
        v-if="props.hasValueIndication"
        class="text-[#151515] dark:text-white text-sm sm:text-base font-medium w-16 text-end mb-2 sm:mb-0 truncate"
        :class="props.valueIndicatorClass"
        :title="value"
        @click="emit('value-indicator-clicked')"
      >

        {{ value }}
      </span>
    </div>
  </div>
</template>
