<script setup lang="ts">
import type { StatsTable } from "~/components/Base/StatsTable.vue";

export interface EventStatsTableScoreProps {
  /**
   * Home team color.
   */
  team1Color?: string;
  /**
   * Away team color.
   */
  team2Color?: string;
  /**
   * Home team logo URL.
   */
  team1LogoUrl?: string;
  /**
   * Away team logo URL.
   */
  team2LogoUrl?: string;
  /**
   * Data with match score stats.
   */
  statsData?: IEventScoreStats | null;
}
const props = defineProps<EventStatsTableScoreProps>();
const { t } = useI18n();
const stats = computed(() => props.statsData?.stats || []);
const cols = computed<StatsTable[]>(() => {
  const first_col: StatsTable = { label: t("labels.team"), key: "name", align: "left", class: "text-base" };
  const stats_cols: StatsTable[] = stats.value.map((s, index) =>
    ({ label: s.shortName || `Q${(index + 1)}`, tooltip: s.name, key: s.shortName || `Q${(index + 1)}`, align: "center", tdClass: "text-base" }));
  return [first_col, ...stats_cols];
});
const rows = computed(() => {
  const row_home_team = { name: props.statsData?.team_home.name, logoUrl: props.team1LogoUrl, logoBg: props.team1Color, alt: t("labels.team_1_logo") };
  const row_away_team = { name: props.statsData?.team_away.name, logoUrl: props.team2LogoUrl, logoBg: props.team2Color, alt: t("labels.team_2_logo") };

  const row_home_team_stats = stats.value.reduce((acc, stat, index) => {
    const shortName = stat.shortName || `Q${(index + 1)}`;
    acc[shortName] = stat.homeTeamValue.number;
    return acc;
  }, {} as Record<string, any>);

  const row_away_team_stats = stats.value.reduce((acc, stat, index) => {
    const shortName = stat.shortName || `Q${(index + 1)}`;
    acc[shortName] = stat.awayTeamValue.number;
    return acc;
  }, {} as Record<string, any>);
  return [
    { ...row_home_team, ...row_home_team_stats },
    { ...row_away_team, ...row_away_team_stats },
  ];
});
</script>

<template>
  <div>
    <BaseStatsTable :data="rows" :cols="cols">
      <template #cell-name="{ row }">
        <div class="flex items-center gap-1">
          <slot name="default" :row="row">
            <BaseLogo
              :title="row.name"
              :alt="row.name || row.alt"
              :src="row.logoUrl"
              :icon-background="row.logoBg"
              :width="30"
              :height="30"
              no-background
              with-initials
              fit="contain"
            />
          </slot>
        </div>
      </template>
    </BaseStatsTable>
  </div>
</template>
