<script lang="ts" setup>
import { THEME_COLOR } from "~/constants";

export interface StatsTable {
  key: string;
  label: string;
  tooltip?: string;
  align?: "left" | "right" | "center" | "justify";
  class?: string;
  tdClass?: string;
  colClass?: string;
  selected?: boolean;
  hidden?: boolean;
  emptyText?: string;
}
export interface BaseStatsTableProps {
  data?: Record<string, any>[];
  cols?: StatsTable[];
  emptyText?: string;
  hasSelection?: boolean;
  highlightRows?: boolean;
  highlightStart?: "light" | "dark";
  detailRowIndex?: number;
  mobileLayout?: boolean;
}
const props = withDefaults(defineProps<BaseStatsTableProps>(), {
  data: () => [],
  cols: () => [],
  emptyText: "No items",
  highlightStart: "light",
});
const emit = defineEmits(["rowClick"]);
const cols = computed(() => (props.cols || []).filter(c => !c.hidden));
const rows = computed(() => props.data || []);
const totalColumnCount = computed(() => props.hasSelection ? cols.value.length + 1 : cols.value.length);
function onRowClick(row: any) {
  emit("rowClick", row);
}
</script>

<template>
  <div class="relative overflow-x-auto bg-[var(--base-card-bgColor)]">
    <table class="w-full text-[var(--base-tab-text-color)] text-sm font-medium" role="table">
      <thead class="text-[var(--base-secondary-color)]" role="rowgroup">
        <tr
          :class="{
            'bg-[var(--base-card-bgColor)]': props.highlightRows && props.highlightStart === THEME_COLOR.LIGHT,
            'bg-[var(--base-popup-bgColor)]': props.highlightRows && props.highlightStart === THEME_COLOR.DARK,
          }"
          role="row"
        >
          <th v-if="props.hasSelection" scope="col" class="p-4" role="columnheader" />
          <th
            v-for="col in cols"
            :key="col.key"
            v-tooltip="col.tooltip"
            :align="col.align || 'left'"
            :class="col.class"
            scope="col"
            class="px-4 py-3 font-medium"
            role="columnheader"
          >
            <slot
              :name="`column-${col.key}`"
              :col="col"
            >
              {{ col.label || '' }}
            </slot>
          </th>
        </tr>
      </thead>

      <tbody role="rowgroup">
        <template v-if="rows.length">
          <template
            v-for="(row, rowIndex) in rows"
            :key="row.key"
          >
            <tr
              v-if="props.mobileLayout"
              role="row"
              @click="onRowClick(row)"
            >
              <td
                :colspan="totalColumnCount"
                scope="col"
                role="cell"
                class="text-center"
              >
                <div class="flex flex-wrap justify-between text-sm">
                  <slot
                    :name="`row-${row.name}`"
                    :row="row"
                    :label="row.name || '-'"
                  >
                    <span>{{ `${row.homeTeamValue}${row.valueSuffix}` }}</span>
                    <span>{{ row.name }}</span>
                    <span>{{ `${row.awayTeamValue}${row.valueSuffix}` }}</span>
                  </slot>
                </div>
              </td>
            </tr>

            <tr
              :class="{
                'bg-[var(--base-card-bgColor)]': props.highlightRows && (rowIndex + (props.highlightStart === THEME_COLOR.LIGHT ? 1 : 0)) % 2 === 0,
                'bg-[var(--base-playing-tag-bgColor)]': props.highlightRows && (rowIndex + (props.highlightStart === THEME_COLOR.LIGHT ? 1 : 0)) % 2 !== 0,
              }"
              role="row"
              @click="onRowClick(row)"
            >
              <td v-if="props.hasSelection" class="w-4 p-4" role="cell" />

              <td
                v-for="(col, colIndex) in cols"
                :id="`col-${rowIndex}-${colIndex}`"
                :key="col.key"
                :align="col.align || 'left'"
                scope="col"
                class="px-4 py-3"
                :class="col.tdClass"
                role="cell"
              >
                <slot
                  :name="`cell-${col.key}`"
                  :col="col"
                  :row="row"
                  :label="row[col.key] || '-'"
                  :row-index="rowIndex"
                >
                  {{ row[col.key] ?? (col.emptyText ?? '') }}
                </slot>
              </td>
            </tr>

            <tr
              v-if="rowIndex === props.detailRowIndex"
              :id="`details-row-${rowIndex}`"
              class="border-b border-[var(--base-tab-background-color)] border-solid"
              role="row"
            >
              <td
                :colspan="totalColumnCount"
                scope="col"
                role="cell"
              >
                <slot
                  name="detail"
                  :row="row"
                />
              </td>
            </tr>
          </template>

          <slot
            name="footer"
            :data="rows"
          />
        </template>
        <tr
          v-else
          class="even:bg-white odd:bg-[#F7F7F7] dark:even:bg-[#1C2535] dark:odd:bg-[#232F43] border-b hover:bg-gray-50"
          role="row"
        >
          <td
            :colspan="totalColumnCount"
            scope="col"
            class="px-4 py-3"
            role="cell"
          >
            <p class="text-center">
              {{ props.emptyText }}
            </p>
          </td>
        </tr>
      </tbody>
    </table>
  </div>
</template>
