<script setup lang="ts">
export interface EventStatsGeniusProps {
  id: string;
}

const props = withDefaults(defineProps<EventStatsGeniusProps>(), {});

const loading = ref(true);

const iframeURL = `https://livestats.dcd.shared.geniussports.com/u/IBBA/${props.id}/index.html`;

const onIframeError = () => {
  console.error("Error occurred while loading iframe content!");
  loading.value = false;
};

const onIframeLoad = () => {
  const script = document.createElement("script");
  script.src = iframeURL;
  document.head.append(script);

  script.onload = () => {
    loading.value = false;
  };
  script.onerror = () => onIframeError();
};
</script>

<template>
  <div class="iframe-container">
    <div v-show="loading" class="skeletonLoading responsive-iframe" />
    <iframe
      :src="iframeURL"
      title="Genius Stats"
      frameborder="0"
      class="responsive-iframe"
      @load="onIframeLoad"
      @onerror="onIframeError"
    />
  </div>
</template>

<style>
  .iframe-container {
    position: relative;
    width: 100%;
    height: 100%;
    overflow: hidden;
    padding-top: 56.25%;
    /* 16:9 Aspect Ratio */
  }

  .responsive-iframe {
    position: absolute;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    width: 100%;
    height: 100%;
    border: none;
  }
</style>
