<script setup lang="ts">
import type { StatsTable } from "~/components/Base/StatsTable.vue";
import { getLineBreaksStyle } from "~/utils/typography";

export interface EventStatsTableGameProps {
  /**
   * Home team name.
   */
  team1Name?: string;
  /**
   * Away team name.
   */
  team2Name?: string;
  /**
   * Home team color.
   */
  team1Color?: string;
  /**
   * Away team color.
   */
  team2Color?: string;
  /**
   * Home team logo URL.
   */
  team1LogoUrl?: string;
  /**
   * Away team logo URL.
   */
  team2LogoUrl?: string;
  /**
   * Data with match overview stats.
   */
  statsData?: IEventGameStats | null;
  /**
   * If true, the component will be set to full screen view.
   */
  fullSize?: boolean;
}
const props = defineProps<EventStatsTableGameProps>();
const { t } = useI18n();
const stats = computed(() => props.statsData?.stats || []);
const rows = computed(() => stats.value);
const cols = computed<StatsTable[]>(() => {
  const col_home_team: StatsTable = {
    key: "homeTeamValue",
    label: props.statsData?.team_home.name || "-",
    class: "capitalize w-1/3 align-top",
    tdClass: "rtl:pr-0 rtl:pl-2 ltr:pl-0 ltr:pr-2 first:text-center last:text-right max-md:bg-none max-md:bg-transparent",
    align: "center",
  };
  const col_metric: StatsTable = {
    key: "name",
    label: "",
    class: "capitalize w-1/3",
    tdClass: "font-medium text-sm sm:text-base pl-2 pr-2 text-center",
    align: "center",
    hidden: !props.fullSize,
  };
  const col_away_team: StatsTable = {
    key: "awayTeamValue",
    label: props.statsData?.team_away.name || "-",
    class: "capitalize w-1/3 align-top",
    tdClass: "rtl:pr-2 rtl:pl-0 ltr:pl-2 ltr:pr-0 first:text-left last:text-center",
    align: "center",
  };
  return [col_home_team, col_metric, col_away_team];
});

const breakWordStyleHomeTeam = computed(() => getLineBreaksStyle(props.team1Name));
const breakWordStyleAwayTeam = computed(() => getLineBreaksStyle(props.team2Name));
</script>

<template>
  <BaseStatsTable
    class="px-4 overflow-hidden"
    :data="rows"
    :cols="cols"
    :mobile-layout="!props.fullSize"
  >
    <template #column-homeTeamValue>
      <div class="flex flex-col items-center gap-2 xl:flex-row" :class="[props.fullSize ? 'justify-center' : 'justify-start']">
        <slot
          name="column-home-team"
          :title="props.team1Name"
          :alt="props.team1Name"
          :src="props.team1LogoUrl"
          :icon-background="props.team1Color"
        >
          <BaseLogo
            class="shrink-0"
            :title="props.team1Name"
            :alt="props.team1Name || t('labels.team_1_logo')"
            :src="props.team1LogoUrl"
            :icon-background="props.team1Color"
            :width="36"
            :height="36"
            no-background
            with-initials
            fit="contain"
          />
          <span class="normal-case text-[var(--base-tab-text-color)]" :class="breakWordStyleHomeTeam">{{ props.team1Name }}</span>
        </slot>
      </div>
    </template>
    <template #column-name>
      <div />
    </template>
    <template #column-awayTeamValue>
      <div class="flex items-center gap-2 xl:flex-row " :class="[props.fullSize ? 'justify-center flex-col' : 'justify-end flex-col-reverse']">
        <slot
          name="column-away-team"
          :title="props.team2Name"
          :alt="props.team2Name"
          :src="props.team2LogoUrl"
          :icon-background="props.team2Color"
        >
          <span class="normal-case truncate text-[var(--base-tab-text-color)]" :class="breakWordStyleAwayTeam">{{ props.team2Name }}</span>

          <BaseLogo
            class="shrink-0"
            :title="props.team2Name"
            :alt="props.team2Name || t('labels.team_2_logo')"
            :src="props.team2LogoUrl"
            :icon-background="props.team2Color"
            :width="36"
            :height="36"
            no-background
            with-initials
            fit="contain"
          />
        </slot>
      </div>
    </template>
    <template #cell-homeTeamValue="{ row }">
      <div v-if="(row as IEventGameStatsS).homeTeamValue || !isNaN((row as IEventGameStatsS).homeTeamValue)">
        <EventStatsProgress
          start-progress-from="right"
          class="flex-col sm:items-center sm:flex-row"
          :max="(row as IEventGameStatsS).maxValue"
          :value="(row as IEventGameStatsS).homeTeamValue"
          :label="(row as IEventGameStatsS).homeTeamValue"
          :has-value-indication="props.fullSize"
          :value-suffix="(row as IEventGameStatsS).valueSuffix"
          :is-low-value="(row as IEventGameStatsS).homeTeamValue < (row as IEventGameStatsS).awayTeamValue"
        />
      </div>
      <p v-else>
        -
      </p>
    </template>
    <template #cell-awayTeamValue="{ row }">
      <div v-if="(row as IEventGameStatsS).awayTeamValue || !isNaN((row as IEventGameStatsS).awayTeamValue)">
        <EventStatsProgress
          value-position="right"
          class="flex-col-reverse sm:flex-row sm:items-center"
          :max="(row as IEventGameStatsS).maxValue"
          :value="(row as IEventGameStatsS).awayTeamValue"
          :label="(row as IEventGameStatsS).awayTeamValue"
          :has-value-indication="props.fullSize"
          :value-suffix="(row as IEventGameStatsS).valueSuffix"
          :is-low-value="(row as IEventGameStatsS).awayTeamValue < (row as IEventGameStatsS).homeTeamValue"
        />
      </div>
      <p v-else>
        -
      </p>
    </template>
  </BaseStatsTable>
</template>
